import { Box, Button } from '@mui/material'
import { styled } from '@mui/material/styles'

import CheckCircleTwoToneIcon from '@mui/icons-material/CheckCircleTwoTone'
import PersonOffTwoToneIcon from '@mui/icons-material/PersonOffTwoTone'
import axios from 'axios'
import { useAuthHeader } from 'react-auth-kit'
import { useSnackbar } from 'notistack'
import { useNavigate, useParams } from 'react-router'

const ButtonError = styled(Button)(
  ({ theme }) => `
     background: ${theme.colors.error.main};
     color: ${theme.palette.error.contrastText};

     &:hover {
        background: ${theme.colors.error.dark};
     }
    `
)

const ButtonSuccess = styled(Button)(
  ({ theme }) => `
       background: ${theme.colors.success.main};
       color: ${theme.palette.success.contrastText};
  
       &:hover {
          background: ${theme.colors.success.dark};
       }
      `
)

interface BulkActionsProps {
  selectedApplicants: number[]
}

type ResultType = 'pass' | 'fail'

interface SubmitApplicant {
  userId: number
  result: ResultType
}

function BulkActions({ selectedApplicants }: BulkActionsProps) {
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const params = useParams()

  const handleFail = () => {
    const submitApplicants = selectedApplicants.map(id => {
      return {
        userId: id,
        result: 'fail' as ResultType
      }
    })

    handleSubmit(submitApplicants)
  }

  const handlePass = () => {
    const submitApplicants = selectedApplicants.map(id => {
      return {
        userId: id,
        result: 'pass' as ResultType
      }
    })

    handleSubmit(submitApplicants)
  }

  const handleSubmit = (submitApplicants: SubmitApplicant[]) => {
    axios
      .put(`${process.env.REACT_APP_APIHOST}/apply/results?clubId=${params.id}`, submitApplicants, {
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('정상적으로 처리되었습니다.', { variant: 'success' })
          navigate(-1)
        } else {
          throw new Error()
        }
      })
      .catch(err => {
        enqueueSnackbar(`${err.response.data?.message} 다시 시도해 주십시오.`, { variant: 'error' })
      })
  }

  return (
    <>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Box display="flex" alignItems="center">
          <ButtonError sx={{ ml: 1 }} startIcon={<PersonOffTwoToneIcon />} variant="contained" onClick={handleFail}>
            불합격
          </ButtonError>
          <ButtonSuccess sx={{ ml: 1 }} startIcon={<CheckCircleTwoToneIcon />} variant="contained" onClick={handlePass}>
            합격
          </ButtonSuccess>
        </Box>
      </Box>
    </>
  )
}

export default BulkActions
