import { Box, Button, Modal, styled, TextareaAutosize, Typography } from '@mui/material'
import { Answers } from 'src/models/Answers'
import { Form } from 'src/models/Form'

interface UserModalProps {
  open: boolean
  onClose: () => void
  questions: Form
  answer: Answers
}

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const UserModal = ({ open, onClose, questions, answer }: UserModalProps) => {
  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        {questions.question1 && (
          <Box>
            <Typography variant="subtitle1">{questions.question1}</Typography>
            <CustomTextareaAutosize maxRows={4} minRows={3} value={answer.answer1} disabled />
          </Box>
        )}
        {questions.question2 && (
          <Box>
            <Typography variant="subtitle1">{questions.question2}</Typography>
            <CustomTextareaAutosize maxRows={4} minRows={3} value={answer.answer2} disabled />
          </Box>
        )}
        {questions.question3 && (
          <Box>
            <Typography variant="subtitle1">{questions.question3}</Typography>
            <CustomTextareaAutosize maxRows={4} minRows={3} value={answer.answer3} disabled />
          </Box>
        )}
        <Box textAlign="center">
          <Button variant="outlined" onClick={onClose}>
            닫기
          </Button>
        </Box>
      </Box>
    </Modal>
  )
}

export default UserModal
